.navibar-brand {
    font-family: viga;
    font-size: 32px;
    float: left;
    text-shadow: 100px 100px 50upx 0px rgba(15, 15, 15, 0.404);
    color: aliceblue;
    margin-top: 60px;
    margin-left: 40px;
}

.navibar-img {
    color: transparent;
    height: 40px;
    float: right;
    margin-top: 56px;
    margin-right: 10px;
}


.contoh {
    color: white;
    font-size: 15px;
    float: center;
    position: relative;
    padding-top: 200px;
    opacity: 80%;
    text-shadow: 100px 100px 50upx 0px rgba(15, 15, 15, 0.404);

}

.Back {
    background-image: url('./bg.jpg');
    background-size: cover;
    height: 720px;
    text-align: center;
    position: center;
}

.Back .container {
    z-index: 1;
    position: relative;
}

.main-container {
    background-position: center;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.search {
    outline: none;
    padding: 19px 7%;
    border-radius: 20px;
    margin-top: 1px;
    margin-bottom: 20px;
    border: none;
    background: rgba(250, 250, 250, 0.85);
}

.city {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 50px;
    padding: 10px 8%;
    border-radius: 15px;
    background: rgba(250, 250, 250, 0.85);
    box-shadow: 10px 10px 5px 0px rgba(15, 15, 15, 0.404);
}

p {
    margin-top: 10px;
    text-transform: uppercase;
    letter-spacing: 0.05em;
}

.city-temp {
    font-size: 5rem;
    font-weight: bold;
    margin-top: 10px;
    color: #1e2432;
    text-align: center;
}

.city sup {
    font-size: 0.5em;
}

.city-name {
    font-size: 2em;
}

.city-name sup {
    padding: 0.2em 0.6em;
    margin-left: 0.2em;
    border-radius: 30px;
    color: #fff;
    background: #ff8c00;
}

.city-icon {
    margin-top: 10px;
    width: 100px;
    height: 100px;
}

.info {
    display: flex;
    flex-direction: column;
    align-items: center;
}

@media only screen and (max-width: 600px) {
    .search {
        padding: 20px 15%;
    }

    .city {
        padding: 40px 20%;
    }
}